import React from 'react';

interface Good {
  id: number;
  name: string;
  color: string;
}

type Props = {
  goods: Good[],
};

export const GoodsList: React.FC<Props> = (props) => {
  const { goods } = props;

  return (
    <ul>
      {goods.map(good => (
        <li key={good.id} style={{ color: good.color }}>
          {good.name}
        </li>
      ))}
    </ul>
  );
};
